import React, { Fragment } from 'react';
import './Docs.less';
import Header from './Header';

const req = require.context('../src', true, /\.docs\.js$/);
const loadDocs = () => req.keys().map(filename => req(filename));

const Docs = () => (
  <Fragment>
    <Header />
    {Object.values(loadDocs()).map(load => <load.default key={load.default.name} />)}
  </Fragment>
);

export default Docs;



